/* http://meyerweb.com/eric/tools/css/reset/ 
	v2.0 | 20110126
	License: none (public domain)
*/
@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+TC:wght@400;700&display=swap");
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

.nav-bar {
  width: 100%;
  display: flex;
  justify-content: space-between;
  padding: 20px 20px;
}
.nav-bar .user-group {
  display: flex;
  align-items: center;
}
.nav-bar .user-name {
  font-weight: bold;
  margin-right: 20px;
}
.nav-bar .logout-btn {
  border: none;
  background: none;
  font-size: 16px;
  cursor: pointer;
}

@media (max-width: 576px) {
  .nav-bar .user-name {
    display: none;
  }
}
.container {
  max-width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.card {
  width: 500px;
  margin-bottom: 16px;
  background: #fff;
  box-shadow: 0 0 15px 0 rgba(0, 0, 0, 0.15);
}

@media (max-width: 576px) {
  .card {
    width: 90%;
  }
}
.content {
  border-radius: 0 0 10px 10px;
  max-width: 100%;
}

.input {
  border-radius: 10px;
  position: relative;
  padding: 2px;
}
.input .txt {
  width: 100%;
  border: none;
  padding: 12px 0 12px 16px;
  font-size: 16px;
}
.input .txt:focus-visible {
  outline: none;
}
.input .add-btn {
  width: 40px;
  height: 40px;
  position: absolute;
  right: 5px;
  top: 3px;
  border: none;
  background: url("https://github.com/hexschool/webLayoutTraining1st/blob/master/%E5%85%AC%E7%9B%8A%E9%AB%94%E9%A9%97%E7%87%9F-Todolist/plus%201.png?raw=true") no-repeat;
  cursor: pointer;
}

.tab {
  max-width: 100%;
  display: flex;
  justify-content: space-between;
  text-align: center;
  border-radius: 10px 10px 0 0;
  margin-bottom: 0;
}
.tab li {
  width: 34%;
  font-size: 14px;
  padding: 1.15em 2em;
  font-weight: bold;
  color: #9f9a91;
  border-bottom: 2px solid #efefef;
  cursor: pointer;
}
.tab .active {
  color: #333;
  border-bottom: 2px solid #333;
}

.list {
  margin-bottom: 20px;
}

.notodo {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 40px;
}
.notodo p {
  margin-bottom: 20px;
}

.todo {
  width: 88%;
  display: flex;
  align-items: center;
  margin: 2px 48px 0 24px;
  padding: 15px 0;
  border-bottom: 1px solid #e5e5e5;
  position: relative;
}
.todo input {
  width: 80%;
  position: absolute;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  cursor: pointer;
  opacity: 0;
}
.todo span {
  font-size: 14px;
  line-height: 20px;
  padding-left: 35px;
}
.todo span::before {
  content: "";
  position: absolute;
  width: 20px;
  height: 20px;
  border: 1px solid #333;
  border-radius: 5px;
  left: 0;
  cursor: pointer;
}
.todo span::after {
  content: "";
  position: absolute;
  width: 0.45rem;
  height: 15px;
  left: 0.45rem;
  top: 33%;
  transform: rotate(45deg);
  border-right: 3px solid #ffd370;
  border-bottom: 3px solid #ffd370;
  cursor: pointer;
  opacity: 0;
}
.todo input:checked ~ span {
  text-decoration: line-through;
}
.todo input:checked ~ span::before {
  border-color: transparent;
}
.todo input:checked ~ span::after {
  opacity: 1;
}
.todo .del-btn {
  text-decoration: none;
  position: absolute;
  right: -1.5rem;
  color: #333;
  opacity: 0;
}
.todo .del-btn:hover {
  opacity: 1;
}

.list-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 10px 35px 24px 24px;
  font-size: 14px;
}
.list-footer input {
  border: none;
  background: none;
  color: #9f9a91;
  cursor: pointer;
}

*, *::before, *::after {
  box-sizing: border-box;
}

body {
  height: 100vh;
  max-width: 100vw;
  background-image: linear-gradient(175deg, #ffd370 60%, #fff 40%);
  font-family: "Noto Sans TC", serif;
}

@media (max-width: 765px) {
  body {
    background-image: none;
    background: #ffd370;
  }
}/*# sourceMappingURL=App.css.map */