.list{
    margin-bottom: 20px;
}
.notodo{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 40px;
    p{
        margin-bottom: 20px;
    }
}
.todo{
    width: 88%;
    display: flex;
    align-items: center;
    margin: 2px 48px 0 24px;
    padding: 15px 0;
    border-bottom: 1px solid #e5e5e5;
    position: relative;
    input{
        width: 80%;
        position: absolute;
        -webkit-user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
        user-select: none;
        cursor: pointer;
        opacity: 0;
    }
    span{
        font-size: 14px;
        line-height: 20px;
        padding-left: 35px;
        &::before{
            content: "";
            position: absolute;
            width: 20px;
            height: 20px;
            border: 1px solid #333;
            border-radius: 5px;
            left: 0;
            cursor: pointer;
        }
        &::after{
            content: "";
            position: absolute;
            width: 0.45rem;
            height: 15px;
            left: 0.45rem;
            top: 33%;
            transform: rotate(45deg);
            border-right: 3px solid #ffd370;
            border-bottom: 3px solid #ffd370;
            cursor: pointer;
            opacity: 0;
        }
    }
    input:checked ~span{
        text-decoration: line-through;
        &::before{
            border-color: transparent;
        }
        &::after{
            opacity: 1;
        }
    }
    .del-btn{
        text-decoration: none;
        position: absolute;
        right: -1.5rem;
        color: #333;
        opacity: 0;
        &:hover{
            opacity: 1;
        }
    }
}