.input{
    border-radius: 10px;
    position: relative;
    padding: 2px;
    .txt{
        width: 100%;
        border: none;
        padding: 12px 0 12px 16px;
        font-size: 16px;
        &:focus-visible{
            outline: none;
        }
    }
    .add-btn{
        width: 40px;
        height: 40px;
        position: absolute;
        right: 5px;
        top: 3px;
        border: none;
        background: url('https://github.com/hexschool/webLayoutTraining1st/blob/master/%E5%85%AC%E7%9B%8A%E9%AB%94%E9%A9%97%E7%87%9F-Todolist/plus%201.png?raw=true') no-repeat;
        cursor: pointer;
    }
}