.nav-bar{
    width: 100%;
    display: flex;
    justify-content: space-between;
    padding: 20px 20px;
    .user-group{
        display: flex;
        align-items: center;
    }
    .user-name{
        font-weight: bold;
        margin-right: 20px;
    }
    .logout-btn{
        border: none;
        background: none;
        font-size: 16px;
        cursor: pointer;
    }
}

@media(max-width:576px){
    .nav-bar{
        .user-name{
            display: none;
        }
    }
}