.tab{
    max-width: 100%;
    display: flex;
    justify-content: space-between;
    text-align: center;
    border-radius: 10px 10px 0 0 ;
    margin-bottom: 0;
    li{
        width: 34%;
        font-size: 14px;
        padding: 1.15em 2em;
        font-weight: bold;
        color: #9f9a91;
        border-bottom: 2px solid #efefef;
        cursor: pointer;
    }
    .active{
        color: #333;
        border-bottom: 2px solid #333;
    }
}