
.container{
    max-width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.card{
    width: 500px;
    margin-bottom: 16px;
    background: #fff;
    box-shadow: 0 0 15px 0 rgba(0 0 0 /15%);
}
@media(max-width:576px){
    .card{
        width: 90%;
    }
}

.content{
    border-radius: 0 0 10px 10px;
    max-width: 100%;
}
